.projects {
  display: flex;
  flex-direction: column;

  flex: 1;
}
button {
  width: 10em;
  height: 5ex;
  background-image: linear-gradient(135deg, #f34079 40%, #fc894d);
  border: none;
  border-radius: 5px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 15px;
}
